h3 {
    margin-left: 1rem;
}

.sendMessage {
    margin-top: 7rem;
}

.sendMessage button {
    font-weight: bold;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    font-size: 16px;
    padding: 1.1rem 2rem;
    border: 0;
    cursor: pointer;
}

.sendMessage svg  {
    margin-left: -0.3rem;
}


.sendMessage form button {
    width: 20%;
  }
  

.sendMessage form {
    width: 30rem;
    display: flex;
}

.sendMessage input {
    background-color: rgb(247, 247, 247);
    line-height: 1.5;
    width: 100%;
    font-size: 1.5rem;
    color: black;
    outline: none;
    border: none;
    padding: 0.6rem 1rem;
  }

  .messages {
    flex-direction: column;
  }

  @media screen and (max-width: 414px) {
    .sendMessage form {
        width: 20rem;
        margin-left: 2rem;
        margin-bottom: 3rem;
    }

    .sendMessage input {
        line-height: 1rem;
        width: 18rem;
        font-size: 1rem;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
    }

    .sendMessage button {
        padding-right: 3rem;
    }

  }